@import "vars";
@import "mixins";
@import "fonts";
@import "settings";


// Header start ========================================================================================================================================================

// Header top  ========================================================================================================================================================

.header-top {
  padding: 11px 0;
  background-color: #1E212C;
  color: rgba(255,255,255,0.6);
  a {
    color: rgba(255,255,255,0.6);
    transition: all 0.3s ease 0s;
    &:hover {
      color: rgba(255,255,255,1);
    }
  }

  // .header-top__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header-top__available

  &__available {
    @media (max-width: 768px){
      font-size: 0;
        a {
          font-size: 12px;
        }
    }
  }

  // .header-top__phone

  &__phone {}

  // .header-top__actions

  &__actions {
    display: flex;
    align-items: center;
    column-gap: 60px;
  }

  // .header-top__languages

  &__languages {
    position: relative;
    padding-right: 16px;
    background-image: url('../img/down-arrow.svg');
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 12px 12px;
    @media (max-width: 576px){
        position: absolute;
        left: 15px;
        bottom: 5%;
    }
    &:hover {
      > ul {
        display: block;
      }
    }
    > ul {
      box-shadow: 0 0 5px 0 rgba(0,0,0,0.5);
      min-width: 100px;
      position: absolute;
      left: 0;
      top: 100%;
      background-color: #1E212C;
      border: 2px solid #03CEA4;
      border-radius: 4px;
      padding: 5px;
      z-index: 100;
      display: none;
      @media (max-width: 576px){
          top: -60px;
      }
      > li {
        margin-bottom: 5px;
      }
    }
  }

  // .header-top__lang

  &__lang {
    padding-left: 32px;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 20px 12px;
  }

  // .header-top__profile

  &__profile {
    padding-left: 24px;
    background-image: url('../img/profile.svg');
    background-repeat: no-repeat;
    background-size: 16px 16px;
    background-position: left center;
  }

  // .header-top__login

  &__login {}

  // .header-top__register

  &__register {}

  .burger {
    @include burger;
    display: none;
    @media (max-width: 992px){
        display: block;
    }
  }

  // Main menu ========================================================================================================================================================

  .nav {
    > ul {
      display: flex;
      align-items: center;
      column-gap: 32px;
      transition: all 0.3s ease 0s;
    }

    @media (max-width: 992px){
      background: rgb(3,206,164);
      background: linear-gradient(0deg, rgb(224, 248, 243) 55%, rgb(193, 237, 246) 100%);
        .catalog__submenu {
          background: rgb(3,206,164);
          background: linear-gradient(0deg, rgb(224, 248, 243) 55%, rgb(193, 237, 246) 100%);
          .min-container {
            padding: 0;
            height: 100vh;
            overflow-y: scroll;
          }
          .submenu__offers {
           display: none;
          }
          h4 {
            font-size: 16px;
            margin-top: 8px;
            margin-bottom: 8px;
            padding-left: 15px;
            font-weight: 600;
            color:rgb(30, 33, 44);
          }
          ul {
            &:first-child {
              > li {
                &:last-child {
                  > a {
                    color: red;
                  }
                }
              }
            }
            > li {
              width: 100%;
              padding: 6px 15px;
              //border-bottom: 1px solid rgba(66, 69, 81, 0.2);
              > a {
                color: rgb(66, 69, 81);
              }
            }
          }
        }
    }
    // .nav__list

    &__list {}

    // .nav__item

    &__item {}

    // .nav__link

    &__link {}

    @media (max-width: 992px){
      position: absolute;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: #fff;
      z-index: 2;
      transform: translateX(-100%);
      transition: all 0.3s ease 0s;
      box-shadow: 0px 0px 4px 0 rgba(0,0,0,0.2);
      z-index: 100;
      > ul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        > li {
          width: 100%;
          border-bottom: 1px solid rgba(66, 69, 81, 0.2);
          > a {
            display: block;
            width: 100%;
            padding: 10px 15px;
            color: rgb(66, 69, 81);
          }
        }
      }
    }
    &.menu--active {
      @media (max-width: 992px){
        .catalog__items {
          > li:not(:last-child) {
            > a {
              position: relative;
              &:after {
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 10px;
                width: 10px;
                height: 15px;
                background-image: url('../img/arrow-right.svg');
                background-repeat: no-repeat;
                background-size: 8px 14px;
              }
            }
          }
        }
      }
      @media (max-width: 576px){
        .header-top__languages {
          background-image: url('../img/down-arrow-dark.svg');
        }
        .header-top__languages a {
          color: rgb(66, 69, 81) !important;
        }
        .header-top__languages ul a {
          color: #fff !important;
        }
      }
      transform: translateX(0%);
      width: 25%;
      @media (max-width: 992px){
        width: 32%;
      }
      @media (max-width: 768px){
        width: 38%;
      }
      @media (max-width: 576px){
        width: 45%;
      }
      @media (max-width: 480px){
         width: 60%;
      }
      @media (max-width: 400px){
        width: 80%;
     }
    }
  }
}

// Catalog menu ========================================================================================================================================================

.catalog {
  background-color: #fff;
  padding: 20px 0;
  position: relative;
  @media (max-width: 576px){
      padding: 14px 0;
  }
  // .catalog__wrapper

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 130px;
    @media (max-width: 1200px){
        column-gap: 50px;
    }
    @media (max-width: 768px){
      column-gap: 15px;
    }
  }

  // .catalog__box-catalog

  &__box-catalog {
    display: flex;
    align-items: center;
    column-gap: 60px;
    @media (max-width: 1200px){
      column-gap: 30px;
    }
    > ul {
      display: flex;
      column-gap: 40px;
      @media (max-width: 1200px){
        column-gap: 20px;
      }
      > li {
        > a {
          color: rgb(66, 69, 81);
          font-size: 16px;
          font-weight: 700;
          line-height: 160%;
          letter-spacing: 0%;
          transition: color 0.3s ease 0s;
          &:hover {
            color: rgb(3, 206, 164);
          }
        }
        &:last-child {
          > a {
            color: rgb(255, 66, 66);
          }
        }
      }
    }
  }

  // .catalog__logo

  &__logo {}

  // .catalog__items

  &__items {

  }

  // .catalog__item

  &__item {

  }

  // .catalog__link

  &__link {}

  // .catalog__box-actions

  &__box-actions {
    display: flex;
    align-items: center;
    column-gap: 48px;
    flex: auto;
    @media (max-width: 768px){
      column-gap: 20px;
    }
  }

  // .catalog__search

  &__search {
    border: 1px solid rgb(215, 218, 221);
    border-radius: 4px;
    background: rgb(255, 255, 255);
    position: relative;
    width: 100%;
    input {
      color: rgb(154, 156, 165);
      font-weight: 400;
      line-height: 150%;
      letter-spacing: 0%;
      padding: 12px 16px;
      outline: none;
      border: none;
      width: 100%;
      border-radius: 4px;
      @media (max-width: 576px){
          padding: 8px 12px;
      }
      &::placeholder {
        color: rgba(154, 156, 165, 0.6);
      }
    }
    button {
      position: absolute;
      top: 50%;
      right: 0px;
      transform: translateY(-50%);
      outline: none;
      border: none;
      height: 100%;
      background: transparent;
      padding: 0 16px;
      cursor: pointer;
    }
  }

  // .catalog__input-search

  &__input-search {}

  // .catalog__wrap

  &__wrap {
    display: flex;
    flex-shrink: 1;
    margin-left: auto;
  }

  // .catalog__favorite

  &__favorite {
    display: flex;
    align-items: center;
    column-gap: 5px;
    padding-right: 20px;
    cursor: pointer;
    position: relative;
    &::after {
      content:'';
      position: absolute;
      background-image: url('../img/divider.png');
      width: 1px;
      height: 22px;
      top: 0;
      right: 0.5px;
    }
    span {
      font-size: 16px;
      line-height: 100%;
    }
  }

  // .catalog__cart

  &__cart {
    display: flex;
    align-items: center;
    column-gap: 5px;
    padding-left: 20px;
    cursor: pointer;
    span {
      border-radius: 4px;
      background: rgb(3, 206, 164);
      color: #fff;
      font-size: 12px;
      line-height: 150%;
      padding: 0px 7px;
    }
  }


  // Submenus ========================================================================================================================================================

  // .catalog__submenus

  &__submenus {}

  // .catalog__submenu

  &__submenu {
    position: absolute;
    left: 0;
    top: 100%;
    background-color: #fff;
    width: 100%;
    z-index: 100;
    padding: 20px 0 40px;
    border: 1px solid rgb(229, 232, 237);
    transform-origin: 50% 0;
    transition: all 0.3s ease 0s;
    @media (max-width: 992px){
        top: 0;
        left: 100%;
        transition: none 0.3s ease 0s;
    }
    @media (max-width: 480px){
        left: 0;
    }
    &-women {
      transform: scaleY(0);
      &.open {
        transform: scaleY(1);
      }
    }
    &-men {
      transform: scaleY(0);
      &.open {
        transform: scaleY(1);
      }
    }
    &-girls {
      transform: scaleY(0);
      &.open {
        transform: scaleY(1);
      }
    }
    &-boys {
      transform: scaleY(0);
      &.open {
        transform: scaleY(1);
      }
    }
  }

  // .catalog__submenu-women

  &__submenu-women {}

  // .catalog__submenu-men

  &__submenu-men {}

  // .catalog__submenu-girls

  &__submenu-girls {}

  // .catalog__submenu-boys

  &__submenu-boys {}

.submenu {
  display: flex;
  column-gap: 100px;
  @media (max-width: 1200px){
      column-gap: 50px;
  }
  @media (max-width: 992px){
      position: relative;
  }
		// .submenu__item

		&__item {
      h4 {
        color: rgb(30, 33, 44);
        font-weight: 700;
        line-height: 150%;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-bottom: 7px;
      }
      > ul {
        > li {
          margin-bottom: 5px;
          > a {
            color: rgb(66, 69, 81);
            font-weight: 400;
            line-height: 150%;
            transition: color 0.3s ease 0s;
            @media (max-width: 992px){
              color: rgb(66, 69, 81) !important;
            }
            @media (min-width: 993px){
              &:hover {
                color: rgb(3, 206, 164);
              }
            }
          }
        }
      }
      &.offers {
        > ul {
          > li:last-child {
            > a {
              color: rgb(255, 66, 66);
              &:hover {
                text-decoration: underline;
              }
            }
          }
        }
      }
    }

		// .submenu__offers

		&__offers {
      flex-shrink: 1;
      margin-left: auto;
      overflow: hidden;
      padding-left: 68px;
      position: relative;
      @media (max-width: 992px){
          display: none;
          width: 0;
          height: 0;
      }
      &::after {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 100%;
        background-image: url('../img/divider-big.png');
      }
      > img {
        max-width: 285px;
        max-height: 100%;
        object-fit: cover;
        margin-bottom: 16px;
      }
      > p {
        color: rgb(66, 69, 81);
        font-weight: 700;
        line-height: 150%;
        margin-bottom: 16px;
      }
      .btn-outline {
        padding: 12px 24px;
      }
  }
}

