.submenu-close {
  background-color: transparent;
  border: none;
  outline: none;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}

.search-container-sm {
  display: block;
  padding: 10px 15px;
  border-top: 1px solid rgb(215, 218, 221);
  @media (min-width: 577px){
      display: none;
  }
}
// Up to in header ========================================================================================================================================================

.up-to {
  background: rgb(23, 105, 106);
  padding: 8px 0;

  // .up-to__wrap

  &__wrap {
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 12px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0%;
    position: relative;
    max-width: 300px;
    margin: 0 auto;
    &::after {
      content: '';
      position: absolute;
      width: 6px;
      height: 11px;
      right: 18px;
      top: 50%;
      transform: translateY(-50%);
      background-image: url('../img/arrow-right.png')
    }
    &::before {
      content: '';
      position: absolute;
      width: 6px;
      height: 11px;
      left: 18px;
      top: 50%;
      transform: translateY(-50%);
      background-image: url('../img/arrow-left.png')
    }
    a {
      text-decoration-line: underline;
      color: #fff;
      &:hover {
        text-decoration: none;
      }
    }
  }

  // .up-to__content

  &__content {}
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.4);
  z-index: -2;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;
  &.open {
    opacity: 1;
    visibility: visible;
  }
  &.opened {
    opacity: 1;
    visibility: visible;
    z-index: 99;
  }
}


// Hero section start ========================================================================================================================================================

.hero {

  // .hero__slider

  &__slider {
    height: 800px;
    @media (max-width: 1400px){
        height: 700px;
    }
    @media (max-width: 768px){
        height: 500px;
    }
  }

  // .hero__slide

  &__slide {
    position: relative;
    z-index: -3;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      display: block;
    }
  }

  // .hero__content

  &__content {
    position: absolute;
    top: 140px;
    left: calc((100% - var(--content-width-min)) / 2);
    @media (max-width: 1250px){
        left: 15px;
    }
    @media (max-width: 1400px){
        top: 80px;
    }
    @media (max-width: 768px){
        top: 60px;
        text-align: center;
        left: 50%;
        transform: translateX(-50%);
        width: 70%;
    }
    @media (max-width: 480px){
        top: 110px;
    }
  }

  // .hero__suptitle

  &__suptitle {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: var(--gray-900);
    margin-bottom: 12px;
  }

  // .hero__title

  &__title {
    font-weight: 900;
    font-size: 72px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: var(--gray-900);
    margin-bottom: 60px;
    @media (max-width: 1400px){
        margin-bottom: 20px;
    }
    @media (max-width: 992px){
        padding: 0 10px;
        border-radius: 20px;
        background-color: rgba(255,255,255,0.5);
    }
    @media (max-width: 768px){
        font-size: 51px;
    }
    @media (max-width: 576px){
        font-size: 42px;
    }
    @media (max-width: 480px){
        font-size: 30px;
    }
  }

  // .hero__btns

  &__btns {
    max-height: 52px;
    display: flex;
    align-items: stretch;
    column-gap: 24px;
    @media (max-width: 768px){
        justify-content: center;
    }
    @media (max-width: 576px){
        flex-direction: column;
        row-gap: 10px;
        max-height: none;
    }
  }

  // .hero__btn

  &__btn {
    height: 100%;
    font-size: 16px;
    padding: 15px 40px;
    @media (max-width: 576px){
        justify-content: center;
        max-width: max-content;
        margin: 0 auto;
        padding: 10px 20px;
    }
  }

  .swiper {
  }
  .swiper-wrapper {
  }
  .swiper-slide {
  }
  .swiper-pagination {
    left: calc((100% - var(--content-width-min)) / 2);
    bottom: 20%;
    text-align: left;
    @media (max-width: 1250px){
        left: initial;
    }
    @media (max-width: 992px){
     bottom: 10%;
    }
    @media (max-width: 768px){
        text-align: center;
    }
  }
  .swiper-pagination-bullet {
    background-color: transparent !important;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--gray-800);
    position: relative;
    min-width: 172px;
    height: 40px;
    padding-bottom: 10px;
    border-radius: 0;
    border-bottom: 2px solid var(--gray-800);
    @media (max-width: 1400px){
        min-width: 100px;
    }
    @media (max-width: 768px){
        opacity: 0.5;
    }
    @media (max-width: 480px){
        min-width: 50px;
        font-size: 20px;
        height: 30px;
    }
  }
  .swiper-pagination-bullet-active {
    @media (max-width: 768px){
        opacity: 1;
    }
  }
  .swiper-button-prev:after,
  .swiper-button-next:after {
    display: none;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-image: url('../img/hero-button-prev.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 48px 48px;
    @media (max-width: 768px){
        width: 36px;
        height: 36px;
    }
  }
  .swiper-button-next {
    background-image: url('../img/hero-button-prev.png');
  }

  .swiper-button-next {
    background-image: url('../img/hero-button-next.png');
  }

}

// Hero section end ========================================================================================================================================================

// Section subhero start ========================================================================================================================================================

.subhero {

  // .subhero__wrap

  &__wrap {
    transform: translateY(-60px);
    display: flex;
    column-gap: 30px;
    position: relative;
    z-index: 98;
    @media (max-width: 992px){
        transform: translateY(0);
        padding: 40px 0;
    }
    @media (max-width: 768px){
      column-gap: 10px;
    }
    @media (max-width: 480px){
        padding: 25px 0;
    }
  }

  // .subhero__items

  &__item {
    max-width: 390px;
    text-align: center;
    @media (min-width: 993px){
      &:hover {
        .subhero__img img {
          transform: scale(1.1);
        }
      }
    }
  }

  // .subhero__img

  &__img {
    width: 100%;
    overflow: hidden;
    margin-bottom: 16px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.3s ease 0s;
    }
    @media (max-width: 768px){
        margin-bottom: 10px;
    }
  }

  // .subhero__title

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--gray-800);
    @media (max-width: 768px){
        font-size: 16px;
        line-height: 120%;
    }
    @media (max-width: 480px){
        font-size: 14px;
    }
  }
}

// Section subhero end ========================================================================================================================================================

// Card start ========================================================================================================================================================

.card {
  max-width: 390px;
  @media (min-width: 993px){
    &:hover {
      .card__info {
        background-color: var(--white);
        box-shadow: 0 4px 4px -4px rgba(30, 33, 44, 0.05), 0 12px 10px -6px rgba(154, 156, 165, 0.08), 0 30px 24px -10px rgba(154, 156, 165, 0.1), 0 80px 80px -20px rgba(154, 156, 165, 0.16);
      }
      .card__actions {
        transform: scale(1);
      }
      .swiper-button-card-prev,
      .swiper-button-card-next {
        cursor: pointer;
        opacity: 1;
      }
      .swiper-button-disabled {
        opacity: 0.35;
      }
    }
  }

  // .card__wrap

  &__wrap {}

  // .card__images

  &__images {
    position: relative;
  }

  // .card__discount

  &__discount {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: var(--white);
    padding: 6px 8px;
    background-color: var(--danger);
    position: absolute;
    top: 16px;
    left: 16px;
    z-index: 2;
    &.none {
      display: none;
    }
  }

  // .card__raiting

  &__raiting {
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 2;
    cursor: pointer;
    &.none {
      display: none;
    }
  }

  // .card__favorite

  &__favorite {
    position: absolute;
    right: 16px;
    bottom: 16px;
    z-index: 2;
    width: 32px;
