        border: 1px solid var(--primary);
      }
      ~ span {
        border: 1px solid var(--gray-400);
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        text-align: center;
        color: var(--gray-800);
