  // .offers__item

  &__item {
    background-repeat: no-repeat;
    background-position: left center;
    background-size: cover;
    padding: 60px;
    border-radius: 4px;
    &:nth-child(1) {
      grid-column: 1 / 3;
      @media (max-width: 1200px){
          grid-column: 1 / 2;
      }
    }
    &:nth-child(2) {
      grid-column: 3 / 6;
      position: relative;
      @media (max-width: 1200px){
        grid-column: 2 / 3;
