  &__list {
    display: grid;
    grid-template-columns: 285px 330px 330px 285px;
    grid-template-rows: auto;
    @media (max-width: 1260px){
      grid-template-columns: repeat(4, 1fr);
    }
    @media (max-width: 992px){
      grid-template-columns: 1fr 1fr;
      row-gap: 40px;
    }
    @media (max-width: 576px){
      grid-template-columns: 1fr;
    }
  }

  // .services__item

  &__item {
    text-align: center;
    position: relative;
    padding: 0 45px;
    @media (max-width: 1260px){
        padding: 0 25px;
    }
    @media (max-width: 992px){
        padding: 0;
    }
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 1px;
        height: 136px;
        background-image: url('../img/divider-services.png');
        @media (max-width: 576px){
            transform: rotate(90deg) translateX(-50%) translateY(0);
            right: auto;
            left: 50%;
            top: auto;
            bottom: -75%;
        }
      }
    }
    &:last-child {
      position: relative;
      @media (max-width: 576px){
        &:after {
          content: '';
          position: absolute;
          width: 1px;
          height: 136px;
          background-image: url('../img/divider-services.png');
          transform: rotate(90deg) translateX(-50%) translateY(0);
          right: auto;
          left: 50%;
          top: auto;
          bottom: -75%;
        }
      }
    }
    @media (max-width: 992px){
      &:nth-child(2) {
        &:after {
          content: none;
          @media (max-width: 576px){
              content: '';
          }
        }
      }
    }
    &:hover {
      .services__icon {
        svg {
          path {
            fill: var(--success)
          }
        }
      }
    }
  }

  // .services__icon

  &__icon {
    max-width: 48px;
    max-height: 48px;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 24px;
    svg {
      width: 100%;
      height: 100%;
      path {
        transition: all 0.3s ease 0s;
      }
    }
  }

  // .services__title

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--gray-900);
    margin-bottom: 8px;
    @media (max-width: 1200px){
        font-size: 18px;
        line-height: 130%;
    }
    @media (max-width: 576px){
        font-size: 16px;
    }
  }

  // .services__descr

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--gray-700);
    @media (max-width: 576px){
        font-size: 14px;
    }
  }
}

// Section services end ========================================================================================================================================================

