// Section subscribe start ========================================================================================================================================================

.subscribe {
  padding: 65px 0;
  background-color: var(--gray-200);

  // .subscribe__wrap

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 30px;
    @media (max-width: 992px){
        flex-direction: column;
        row-gap: 40px;
        align-items: flex-start;
    }
  }

  // .subscribe__info

  &__info {
    @media (max-width: 992px){
        width: 100%;
    }
  }

  // .subscribe__title

  &__title {
    margin-bottom: 24px;
    @media (max-width: 480px){
        margin-bottom: 16px;
    }
  }

  // .subscribe__subtitle

  &__subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--gray-700);
    margin-bottom: 32px;
    @media (max-width: 480px){
        margin-bottom: 24px;
        font-size: 16px;
    }
  }

  // .subscribe__form

  &__form {}

  // .subscribe__gender

  &__gender {
    display: flex;
    flex-wrap: wrap;
    column-gap: 12px;
    row-gap: 12px;
    margin-bottom: 32px;
    @media (max-width: 400px){
        margin-bottom: 24px;
    }
    input[type="radio"] {
      display: none;
      outline: none;
      border: none;
      &:checked ~ span {
        background-color: var(--primary);
        color: var(--white);
      }
      ~ span {
        display: inline-block;
        border: 1px solid var(--gray-400);
        border-radius: 4px;
        padding: 5px 16px;
        font-weight: 700;
        line-height: 100%;
        text-align: center;
        color: var(--gray-700);
        cursor: pointer;
        @media (max-width: 480px){
            font-size: 12px;
        }
      }
    }
  }

  // .subscribe__input

  &__input {
    label {
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-800);
      display: inline-block;
      margin-bottom: 8px;
    }
  }

  // .subscribe__email

  &__email {}

  // .subscribe__input-wrap

  &__input-wrap {
    position: relative;
    width: 495px;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 20px;
    @media (max-width: 992px){
        width: 100%;
    }
    @media (max-width: 400px){
      width: 100%;
      max-width: 100%;
    }
    input {
      padding: 13px 0 13px 16px;
      font-size: 16px;
      font-weight: 400;
      line-height: 100%;
      color: var(--gray-900);
      outline: none;
      border: none;
      background-color: var(--white);
      width: 100%;
      &::placeholder {
        opacity: 0.5;
      }
      @media (max-width: 400px){
          font-size: 14px;
      }
    }
  }

  // .subscribe__btn

  &__btn {
    position: absolute;
    font-size: 16px;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    outline: none;
    border: none;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: 0.04em;
    color: var(--white);
    background: var(--primary);
    height: 100%;
    border-radius: 0 4px 4px 0;
    padding: 0 32px;
    cursor: pointer;
    @media (max-width: 480px){
        padding: 0 10px;
        font-size: 14px;
    }
  }

  // .subscribe__agree

  &__agree {
    display: flex;
    align-items: center;
    column-gap: 28px;
    position: relative;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--gray-800);
    cursor: pointer;
    @media (max-width: 480px){
        font-size: 14px;
    }
    span {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      border-radius: 3px;
      background-color: var(--primary);
      @media (max-width: 400px){
          top: 0;
          transform: translateY(0%);
      }
      &:before {
        content: '';
        position: absolute;
        left: 3px;
        top: 50%;
        transform: translateY(-50%);
        width: 10px;
        height: 8px;
        background-image: url('../img/check.png');
        z-index: 1;
        opacity: 0;
      }
    }
    input {
      width: 0;
      height: 0;
    }
    input[type="checkbox"]:checked ~ span:before {
      opacity: 1;
    }
  }

  // .subscribe__img

  &__img {
    @media (max-width: 1200px){
        img {
          width: 400px;
        }
    }
    @media (max-width: 992px){
        align-self: center;
    }
    @media (max-width: 480px){
      text-align: center;
        img {
          width: 70%;
        }
    }
  }
}

