html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,h2,h3,h4,h5,h6,p,ul,ol {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 14px;
  background-color: var(--white);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  //overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.max-container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-max);
}

.min-container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-min);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  display: inline-flex;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: var(--white);
  border-radius: 4px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 1;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
    z-index: -1;
    transition: all 0.3s ease 0s;
    border-radius: 4px;
    background-color: var(--primary);
  }
  &:hover {
    color: var(--primary);
  }
  &:hover {
    &:after {
      width: 0%;
    }
  }
}

.btn-outline {
  color: rgb(23, 105, 106);
  font-size: 12px;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: 0.5px;
  border: 1px solid rgb(23, 105, 106);
  border-radius: 4px;
  transition: all 0.3s ease 0s;
  display: inline-flex;
  align-items: center;
  column-gap: 10px;
  position: relative;
  z-index: 1;
  &:after {
    content: '';
    position: absolute;
    width: 0%;
    height: 100%;
    background-color: rgb(23, 105, 106);
    left: 0;
    top: 0;
    z-index: -1;
    transition: all 0.3s ease 0s;
  }
  svg {
    transition: all 0.3s ease 0s;
  }
  &:hover {
    color: #fff;
    &:after {
      width: 100%;
    }
    svg {
      path {
        fill: #fff;
      }
    }
  }
}

.title {
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: var(--gray-900);
  @media (max-width: 768px){
      font-size: 36px;
  }
  @media (max-width: 576px){
      font-size: 32px;
  }
}
